.rel-brick {
  background-color: #ddd;
  border: 2px solid #fff;
  min-height: 30px;
  height: 30px;
}

.rel-nav {
  height: 40px;
  width: 100%;
  padding: 0;
  display: block;
  position: relative;
  top: 0;
  left: 0;
  background: #369;
  color: #fff;
}

.rel-nav a {
  color: inherit;
  /* blue colors for links too */
  text-decoration: inherit;
  /* no underline */
  line-height: 40px;
}

.rel-nav>a {
  width: 40px;
  padding: 0;
  font-size: 25.6px;
  text-align: center;
  transition: margin-left 0.3s;
}

.rel-nav #menu {
  height: 100vh;
  width: 0;
  position: fixed;
  top: 0;
  left: 0;
  padding: 0;
  background: #fff;
  color: #369;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: width 0.3s;
  overflow-x: hidden;
  z-index: 999;
}

.rel-nav #menu ul {
  display: none;
  height: 100%;
  width: 100%;
  list-style: none;
}

.rel-nav #menu ul>li {
  float: left;
  height: 40px;
  line-height: 40px;
  cursor: pointer;
  width: 100%;
}

.rel-nav #menu:target {
  display: block;
  width: 300px;
}

.rel-nav #menu:target~a {
  margin-left: 300px;
}

.rel-nav #menu:target~#show {
  display: none;
}

.rel-nav #menu:target~#hide {
  display: block;
}

.rel-nav #hide {
  display: none;
}

.rel-zoom {
  overflow: scroll;
}

.rel-tree * {
  width: initial;
  clear: both;
  padding: 0 0 0 40px;
}

.rel-tree label {
  cursor: pointer;
}

.rel-tree ul {
  display: none;
}

.rel-tree input[type='checkbox'] {
  display: none;
}

.rel-tree input[type='checkbox']:checked+ul {
  display: block;
}

.rel-tabs {
  position: relative;
  width: initial;
  width: 100%;
  /*height: 300px;*/
  background-color: #369;
  padding: 2px;
}

.rel-tabs>*,
.rel-tabs>label>* {
  float: left;
  width: initial;
}

.rel-tabs table * {
  float: none;
}

.rel-tabs>label {
  haight: auto;
  width: auto\0;
  height: 20px \0;
  line-height: 6px \0;
}

@media screen and (min-height: 1px) {
  .rel-tabs>label {
    width: initial;
  }
}

.rel-tabs>label>span {
  cursor: pointer;
  display: inline-block;
  width: 100%;
  box-sizing: border-box;
}

.rel-tabs>label>span>span~span {
  background-color: #a0a0a0;
  color: #fff;
  padding: 0 3px 0 3px;
  margin: 3px 0 0 3px;
  line-height: 29px;
  height: 26px;
}

.rel-tabs>label>input[type=radio] {
  position: absolute;
  height: 0;
  width: 0;
}

.rel-tabs>label>input[type=radio]:checked~span,
.rel-tabs>label>input[type=radio]:checked {
  background-color: #fff;
}

.rel-tabs>label>input[type=radio]:focus~span,
.rel-tabs>label>input[type=radio]:focus {
  background-color: #fff;
}

.rel-tabs>label>input[type=radio]:checked~span~div,
.rel-tabs>label>input[type=radio]:checked~div {
  display: table;
  width: 100%;
}

.rel-tabs>label>input[type=radio]:focus~span~div,
.rel-tabs>label>input[type=radio]:focus~div {
  display: table;
  width: 100%;
}

.rel-tabs>label>input[type=radio]:hover~span~div,
.rel-tabs>label>input[type=radio]:hover~div {
  display: block\9;
}

.rel-tabs>label>div {
  position: absolute;
  top: 42px;
  bottom: 0;
  left: 0;
  right: 0;
  display: none;
  text-align: left;
  text-align: initial;
  background-color: #fff;
  overflow-y: scroll;
}

.rel-tabs>label:first-child>div {
  display: block\9;
}

.rel-tabs>label {
  text-align: center;
}

.rel-tabs>label>span {
  background-color: #369;
  color: #fff;
  padding: 4px 6px 4px 6px;
}

.rel-tabs>label>input[type=radio]:checked~span {
  color: #000;
  border-bottom: 1px solid #000;
}

.rel-tabs>label>input[type=radio]:focus~span {
  color: #000;
  border-bottom: 1px solid #000;
}

.rel-tabs>label>div {
  padding: 15px;
  border: 1px solid #a0a0a0;
  border-top: 0px solid #fff;
}

.gutter,
input,
iframe,
div,
select,
label,
textarea,
button,
ul,
li,
a,
pre,
main {
  padding: 3px;
}

.nogutter {
  padding: 0;
}

.margin-gutter,
select,
input,
button,
textarea {
  margin-top: 1px;
  margin-bottom: 1px;
}


/*
	Sets default sizing and behavior of all possible elements.
	This affects including elements generated by libraries that don't reset it's styles.
*/

input,
iframe,
div,
select,
label,
textarea,
button,
ul,
li,
a,
pre,
main {
  float: left;
  box-sizing: border-box;
  display: inline-block;
  vertical-align: top;
  width: 100%;
}

@media (min-height: 1px) {
  input,
  iframe,
  div,
  select,
  label,
  textarea,
  button,
  ul,
  li,
  a,
  pre,
  main {
    min-height: 32px;
  }
}

input[type=checkbox],
input[type=radio] {
  width: auto;
  min-height: initial;
  padding: 0;
}

label {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow-x: hidden;
  padding: 0;
}

label>* {
  margin: 0;
}

label>select,
label+select {
  margin-top: initial;
}

h1 {
  font-size: 40px;
  font-weight: 500;
  float: left;
  width: 100%;
  margin-top: 16px;
}

h2 {
  font-size: 32px;
  font-weight: 500;
  float: left;
  width: 100%;
  margin-top: 16px;
}

h3 {
  font-size: 28px;
  font-weight: 500;
  float: left;
  width: 100%;
  margin-top: 16px;
}

h4 {
  font-size: 20px;
  font-weight: 500;
  float: left;
  width: 100%;
  margin-top: 16px;
}

h5 {
  font-size: 16px;
  font-weight: 500;
  float: left;
  width: 100%;
  margin-top: 16px;
}

h6 {
  font-size: 12px;
  font-weight: 500;
  float: left;
  width: 100%;
  margin-top: 16px;
}


/*
	Max width containers for better view with bigger screens and small content.
*/

iframe {
  border: 0 none;
}

fieldset {
  border: 1px solid #c0c0c0;
}

label>[type=checkbox]+span,
label>[type=radio]+span {
  cursor: pointer;
}

table {
  border: 0;
  border-collapse: collapse;
}

table tr,
table th,
table td {
  border: 0;
}

table th,
table td {
  padding: 0 5px 0 5px;
  padding-left: 5px;
  padding-right: 5px;
  height: 30px;
  font-weight: normal !important;
}

table th {
  line-height: 25px;
  background-color: #369;
  border: 1px solid #fff;
  color: #fff;
  font-weight: bold;
  cursor: default;
}

* {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 16px;
  margin: 0;
}

input,
select,
textarea,
button {
  display: block;
  min-height: 22px;
  height: 16px;
  padding: 3px 0 3px 0;
  border-radius: 3px;
}

@media (min-height: 1px) {
  input,
  select,
  textarea,
  button {
    min-height: 32px;
    height: 32px;
  }
}

input:not([type=checkbox]):not([type=radio]):not([type=submit]),
select,
textarea {
  border: 1px solid #c0c0c0;
}

label {
  min-height: 16px;
  line-height: 16px;
}

@media (min-height: 1px) {
  label {
    line-height: 32px;
  }
}

button,
input[type=submit] {
  background-color: #369;
  color: #fff;
  border: none;
  padding: 0 16px 0 16px;
}

input[type=checkbox],
input[type=radio] {
  margin-right: 5px;
}

button,
input[type=submit],
select:not([multiple=true]) {
  cursor: pointer;
}

pre {
  background: #eee;
  border-left: 2px solid #369;
  overflow: auto;
}

